*{
    margin: 0;
    padding: 0;
}

.error-page{
    text-align: center;
    margin-top: 150px;
    width: 50%;
    justify-content: center;
    margin-left: 25%;
}

.error-page h1{
    font-size: 4rem;
    padding: 30px;
    padding-bottom: 5px;
}

.error-page p{
    font-size: 2rem;
    padding-bottom: 5px;
}

.error-page span{
    font-size: 1.2rem;
}

@media (max-width: 1049px){
    .error-page{
        width: 70%;
        margin-left: 15%;
        padding-top: 5px;
        margin-top: 70px;
    }
}

