*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.header{
    position: relative;
    background-color: #000;
    color: #fff;
    padding: 30px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h1{
    font-size: 2rem;
    font-family: sans-serif;
    font-weight: 700;
    cursor: pointer;
    padding-left: 30px;
    background-color: #000;
    text-decoration: underline;
    text-decoration-color: #555;
}

.header span{
    color: #2C516D;
    background-color: #000;
}

.header .nav ul{
    display: flex;
    background-color: #000;
}

.nav li{
    list-style-type: none;
    background-color: #000;
}

.nav li a{
    text-decoration: none;
    color: rgb(187, 187, 187);
    padding-right: 30px;
    font-family: sans-serif;
    font-size: 1.2rem;
    transition: .5s ease;
    background-color: #000;
}

.nav li a:hover{
    color: #fff;
}

@media (max-width: 1049px) {
    .header{
        flex-direction: column;
        align-items: center;
        padding: 15px 20px;
    }

    .header h1{
        padding: 20px 10px;
    }

    .header ul{
        padding: 10px;
    }

    .header li{
        padding: 5px;
    }

    .header li a{
        background-color: #fff;
        border-radius: 5px;
        color: #011627;
        padding: 10px;
    }

    .header li a:hover{
        color: #fff;
        background-color: #011627;
    }
}