*{
    padding: 0;
    margin: 0;
    background-color: #111;
    color: #fff;
    font-family: sans-serif;
}

.countainer{
    padding-top: 100px;
    padding-left: 100px;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

.countainer .tutorial-text{
    padding: 50px 40px;
    width: 50%;
    box-shadow: 2px 2px 8px #fff;
    border-radius: 5px;
}

.countainer .tutorial-text h1{
    color: #fff;
    font-family: sans-serif;
    font-size: 2rem;
    padding: 10px;
}

.countainer .tutorial-text p{
    color: #fff;
    font-size: 1rem;
    font-family: sans-serif;
    padding: 15px;
    padding-top: 0;
}

.countainer .tutorial-text2{
    width: 30%;
    padding-right: 100px;
    box-shadow: 2px 2px 8px #fff;
    padding: 40px 50px;
    transition: .5s ease;
}

.countainer .tutorial-text2:hover{
    margin-right: 30px;
}

.container .tutorial-text2 p{
    color: #fff;
    font-family: sans-serif;
    font-size: 1.1rem;
}

@media (max-width: 1049px) {
    .countainer{
        flex-direction: column;
        align-items: center;
        padding: 10px;
        margin: 0;
        padding-top: 30px;
    }

    .countainer .tutorial-text{
        padding: 20px;
        width: 90%;
        margin-bottom: 30px;
    }

    .countainer .tutorial-text h1{
        font-size: 1.6rem;
    }

    .countainer .tutorial-text2{
        width: 70%;
        padding: 30px;
    }

    .countainer .tutorial-text2 p{
        font-size: 1rem;
    }
}