*{
    margin: 0;
    padding: 0;
}

.contact-page{
    text-align: center;
    margin-top: 50px;
    width: 50%;
    justify-content: center;
    margin-left: 10%;
}

.contact-page .content{
    background-color: #222;
    padding: 80px 70px;
    margin: 0;
    width: 100%;
    text-align: center;
    margin-left: 25%;
    border-radius: 10px;
}

.contact-page h1{
    padding-top: 5px;
    padding-left: 45%;
    padding-bottom: 10px;
}

.content input{
    width: 100%;
    padding: 10px 20px;
    margin-bottom: 5px;
    font-size: 1.2rem;
    border-radius: 5px;
    border: 1px solid #000;
    outline: none;
}

.content textarea{
    height: 100px;
    border: 1px solid #000;
    font-size: .8rem;
    resize: none;
    outline: none;
    width: 100%;
    margin-bottom: 5px;
}

.content button{
    width: 100%;
    padding: 10px 20px;
    font-size: 1rem;
}

.content button:hover{
    background-color: #000;
    color: aliceblue;
    cursor: pointer;
}

@media (max-width: 1049px){
    .contact-page{
        width: 70%;
        margin-left: 0%;
    }
}