*{
    margin: 0;
    padding: 0;
    background-color: #111;
}

.container{
    padding-top: 100px;
    padding-left: 100px;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

.container .home-text{
    padding: 50px 40px;
    width: 50%;
    box-shadow: 2px 2px 8px #fff;
}

.container .home-text h1{
    color: #fff;
    font-family: sans-serif;
    font-size: 2rem;
    padding: 10px;
}

.container .home-text p{
    color: #fff;
    font-size: 1rem;
    font-family: sans-serif;
    padding: 15px;
    padding-top: 0;
}

.container .home-text .my-button a{
    color: #fff;
    margin-right: 5px;
    text-decoration: none;
    background-color: #fff;
    color: #011627;
    padding: 15px 30px;
    font-family: sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
}

.container .home-text a{
    color: #fff;
    margin-right: 5px;
    text-decoration: none;
    background-color: #fff;
    color: #011627;
    padding: 10px;
    font-family: sans-serif;
    border-radius: 5px;
    transition: .5s ease;
}

.container .home-text a:hover{
    background-color: #0a2c47;
    color: #fff;
}

.container .home-text2{
    width: 30%;
    padding-right: 100px;
    box-shadow: 2px 2px 8px #fff;
    padding: 40px 50px;
    transition: .5s ease;
}

.container .home-text2:hover{
    margin-right: 30px;
}

.container .home-text2 p{
    color: #fff;
    font-family: sans-serif;
    font-size: 1.1rem;
}

@media (max-width: 1049px) {
    .container{
        flex-direction: column;
        align-items: center;
        padding: 10px;
        margin: 0;
        padding-top: 30px;
    }

    .container .home-text{
        padding: 20px;
        width: 90%;
        margin-bottom: 30px;
    }

    .container .home-text h1{
        font-size: 1.6rem;
    }

    .container .home-text .my-button{
        margin-bottom: 0px;
    }

    .container .home-text .my-button a{
        padding: 10px;
    }

    .container .home-text .link a{
        display: none;
    }

    .container .home-text2{
        width: 70%;
        padding: 30px;
    }

    .container .home-text2 p{
        font-size: 1rem;
    }
}